  i.e. .is-selected, .hover-container > .hover-show

*********************/
.hover-show {
  opacity: 0;
  transition: opacity 0.3s ease-in-out; }

.hover-container:hover .hover-show {
  opacity: 1; }

.hover-danger:hover {
  color: #e05b18; }

.hover-row:hover {
  background: #f3f3f4; }

.pointer {
  cursor: pointer; }

.is-open {
  height: auto; }

.is-closed {
  height: 0; }

