/**********

  Triangle Shape Component

**************************

  A simple component for creating a triangle shape in several directions

*************************/
.triangle-bottom {
  border-left: 0.625rem solid transparent;
  border-right: 0.625rem solid transparent;
  border-top: 0.625rem solid;
  border-top-color: inherit; }

.triangle-left {
  border-bottom: 0.625rem solid transparent;
  border-right: 0.625rem solid;
  border-right-color: inherit;
  border-top: 0.625rem solid transparent; }

.triangle-right {
  border-bottom: 0.625rem solid transparent;
  border-left: 0.625rem solid;
  border-left-color: inherit;
  border-top: 0.625rem solid transparent; }

.triangle-top {
  border-bottom: 0.625rem solid;
  border-bottom-color: inherit;
  border-left: 0.625rem solid transparent;
  border-right: 0.625rem solid transparent; }

.modal-background {
  background-color: rgba(0, 0, 0, 0.6); }

.tooltip {
  background-color: #13293f;
  border-radius: 6px;
  display: block;
  line-height: normal;
  padding: 0.625rem; }
  .tooltip.top-left .triangle-top {
    left: 1.875rem;
    top: -0.625rem; }
  .tooltip.top-center .triangle-top {
    left: 50%;
    margin-left: -0.625rem;
    top: -0.625rem; }
  .tooltip.top-right .triangle-top {
    right: 1.875rem;
    top: -0.625rem; }
  .tooltip.right-top .triangle-right {
    right: -0.625rem;
    top: 1.875rem; }
  .tooltip.right-center .triangle-right {
    margin-top: -0.625rem;
    right: -0.625rem;
    top: 50%; }
  .tooltip.right-bottom .triangle-right {
    bottom: 1.875rem;
    right: -0.625rem; }
  .tooltip.bottom-right .triangle-bottom {
    bottom: -0.625rem;
    right: 1.875rem; }
  .tooltip.bottom-center .triangle-bottom {
    bottom: -0.625rem;
    left: 50%;
    margin-left: -0.625rem; }
  .tooltip.bottom-left .triangle-bottom {
