.linear {
  animation-timing-function: linear; }

.ease-in {
  animation-timing-function: ease-in; }

.ease-out {
  animation-timing-function: ease-out; }

.ease-in-out {
  animation-timing-function: ease-in-out; }

.anim-fade {
  animation-duration: 0.3s;
  animation-fill-mode: forwards;
  animation-name: fade;
  opacity: 0; }

@keyframes fade {
  0% {
    opacity: 0; }
  100% {
    opacity: 1; } }

.c-fade {
  transition: color 0.1s; }

.anim-fade-top-appear {
  opacity: 0;
  transform: translateY(5px);
  transition: all 0.1s ease-out; }
  .anim-fade-top-appear.anim-fade-top-appear-active {
    opacity: 1;
    transform: translateY(0px); }

.anim-fade-right-appear {
  opacity: 0;
  transform: translateX(5px);
  transition: all 0.1s ease-out; }
  .anim-fade-right-appear.anim-fade-top-active-appear {
    opacity: 1;
    transform: translateX(0px); }

.anim-fade-bottom-appear {
  opacity: 0;
  transform: translateY(-5px);
  transition: all 0.1s ease-out; }
  .anim-fade-bottom-appear.anim-fade-top-active-appear {
    opacity: 1;
    transform: translateY(0px); }

.anim-fade-left-appear {
  opacity: 0;
  transform: translateX(-5px);
  transition: all 0.1s ease-out; }
  .anim-fade-left-appear.anim-fade-top-active-appear {
    opacity: 1;
    transform: translateX(0px); }

.zoom {
  animation-duration: 0.2s;
  animation-fill-mode: forwards;
  animation-name: zoom;
  transform: scale(0); }

@keyframes zoom {
  0% {
    transform: scale(0); }
  100% {
    transform: scale(1); } }

.anim-drop {
  animation-duration: 0.2s;
  animation-fill-mode: forwards;
  animation-name: drop;
  opacity: 0;
  transform: translateY(-100%); }

@keyframes drop {
  0% {
    opacity: 0;
    transform: translateY(-100%); }
  100% {
    opacity: 1;
    transform: translateY(0); } }

